html, body {
  height: 100%;
  background-color: #151414;
  color: white;
  text-align: center;
}

#page-container {
  position: relative;
  min-height: 92vh;
  
}

#content-wrap {
  padding-bottom: 3rem;    /* Footer height */
}

#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 3rem;            /* Footer height */
}

.caption {
  font-size: 14px;
  padding-top: 5px;
}

.bubble {
  border-radius: 25px;
  background-color: rgb(225, 225, 225, 0.3);
  margin: 40px;
}
.quote {
  margin: 20px;
  font-size: 30px;
}

.author {
  text-align: right;
  padding-bottom: 10px;
  padding-right: 2em;
  font-size: 20px;
}

.section-block {
  padding: 20px;
  background-color: white;
  color: black;
}

.pad-r {
  padding-right: 20px;
}

.pad-l {
  padding-left: 20px;
}

.page-header {
  padding-top: 20px;
  padding-bottom: 20px;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.column {
  max-width:100%;
max-height:100%;
  overflow: hidden;
}

.left {
  width: 40%;
}

.right {
  width: 60%;
}

.header-img {
  max-width: 100%;
  max-height: 100%;
}

.navlink {
  text-align: center;
  color: black;
  text-decoration: none;
  font-size: 26px;
  width: 100%;
}

.navlink:hover,
.navlink:focus {
  color: #999;
  text-decoration: none;
  cursor: pointer;
}

.navbox {
  text-align: center;
  margin-top: 100px;
}

.open {
  color: white;
  position: absolute;
  top: 10px;
  right: 25px;
  font-size: 35px;
  font-weight: bold;
  padding: 0;
  border: none;
  background: none;
}

.open:hover,
.open:focus {
  color: #999;
  text-decoration: none;
  cursor: pointer;
}


.close {
  color: black;
  position: absolute;
  top: 10px;
  right: 25px;
  font-size: 35px;
  font-weight: bold;
  padding: 0;
  border: none;
  background: none;
}

.close:hover,
.close:focus {
  color: #999;
  text-decoration: none;
  cursor: pointer;
}

